:root {
  --bg: #0b0f19;
  --card: #111526;
  --text: #e7e8ee;
  --muted: #aab0c6;
  --accent: #6aa3ff;
  --accent-2: #9bffb6;
  --border: #1e2440;
}

* { box-sizing: border-box; }
html, body, #root { height: 100%; }
body {
  margin: 0;
  font-family: ui-sans-serif, system-ui, -apple-system, Segoe UI, Roboto, Arial;
  background: var(--bg);
  color: var(--text);
}

.container {
  width: min(1100px, 92%);
  margin: 0 auto;
}

.nav {
  position: sticky; top: 0; z-index: 50;
  border-bottom: 1px solid var(--border);
  background: rgba(11,15,25,0.7);
  backdrop-filter: blur(8px);
}
.nav__inner {
  display: flex; align-items: center; justify-content: space-between;
  height: 60px;
}
.brand { font-weight: 800; letter-spacing: 0.5px; }
.nav a {
  color: var(--muted); text-decoration: none; margin-left: 18px; font-size: 0.95rem;
}
.nav a:hover { color: var(--text); }

.section { padding: 64px 0; border-bottom: 1px solid var(--border); }
.section h2 { margin: 0 0 16px; }
.mt { margin-top: 16px; }

.hero h1 { margin: 0 0 10px; font-size: clamp(28px, 6vw, 44px); }
.subtitle { color: var(--muted); margin: 0 0 20px; }
.accent { color: var(--accent); }
.cta { display: flex; gap: 12px; flex-wrap: wrap; }

.btn {
  display: inline-block; padding: 10px 16px; border-radius: 10px;
  background: var(--accent); color: #08111e; text-decoration: none; font-weight: 700;
}
.btn--secondary { background: var(--accent-2); }

.tags { list-style: none; padding: 0; display: flex; gap: 8px; flex-wrap: wrap; }
.tags li {
  background: var(--card); border: 1px solid var(--border);
  padding: 6px 10px; border-radius: 999px; color: var(--muted);
}

.grid {
  display: grid; gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
}
.card {
  background: var(--card); border: 1px solid var(--border);
  border-radius: 14px; padding: 16px;
}
.card h3 { margin-top: 0; }
.link { color: var(--accent); text-decoration: none; }
.link:hover { text-decoration: underline; }

.contact { list-style: none; padding: 0; }
.contact li { margin: 6px 0; }
.contact a { color: var(--accent); text-decoration: none; }
.contact a:hover { text-decoration: underline; }

.footer { padding: 24px 0; color: var(--muted); }
